@import url(normalize.css);
*,
*:before,
*:after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  text-decoration-skip: ink; }

nav ul,
nav ol {
  padding: 0;
  margin: 0;
  list-style: none outside none; }

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0; }

img,
iframe,
video,
audio,
object {
  max-width: 100%; }

img,
iframe {
  border: 0 none; }

img {
  height: auto;
  display: inline-block;
  vertical-align: middle; }

template {
  display: none; }

svg:not(:root) {
  overflow: hidden; }

a,
button,
input[type='submit'],
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='image'],
label[for],
.c-btn {
  cursor: pointer; }

a[href^='tel'],
button[disabled],
input[disabled],
textarea[disabled],
select[disabled],
.c-btn.disabled,
.c-btn[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0; }

input[type='text'],
input[type='password'],
input[type='date'],
input[type='datetime'],
input[type='datetime-local'],
input[type='month'],
input[type='week'],
input[type='email'],
input[type='number'],
input[type='search'],
input[type='tel'],
input[type='time'],
input[type='url'],
input[type='color'],
textarea,
a[href^='tel'] {
  appearance: none; }

input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  appearance: none; }

textarea {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased; }

button,
select {
  text-transform: none; }

input::placeholder {
  color: inherit;
  opacity: 1; }

textarea::placeholder {
  color: inherit;
  opacity: 1; }

input:-webkit-autofill {
  -webkit-text-fill-color: inherit;
  -webkit-box-shadow: 0 0 0 1000px #fff inset; }

a,
a img,
input,
button,
select,
textarea,
.c-btn,
[class^='svg-'] {
  transition-property: color, opacity, border-color, background-color, box-shadow, fill, stroke;
  transition-duration: 0.4s;
  transition-timing-function: ease-in-out; }

html {
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent; }

body {
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1.35;
  color: #333;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-size-adjust: none;
  min-width: 320px;
  margin: 0;
  background-color: #fff; }

h1 {
  font-size: 2rem; }

h2 {
  font-size: 1.75rem; }

h3 {
  font-size: 1.5rem; }

h4 {
  font-size: 1.25rem; }

h5 {
  font-size: 1rem; }

h6 {
  font-size: 0.75rem; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0; }
  h1[class], h1:last-child,
  h2[class],
  h2:last-child,
  h3[class],
  h3:last-child,
  h4[class],
  h4:last-child,
  h5[class],
  h5:last-child,
  h6[class],
  h6:last-child {
    margin-bottom: 0; }

a {
  color: #ef4c23;
  text-decoration: underline; }
  a[href^='tel'] {
    color: inherit; }
  a:hover, a[href^='tel'] {
    text-decoration: none; }

p,
ul,
ol,
dl,
table,
blockquote {
  margin-top: 0; }
  p:last-child,
  ul:last-child,
  ol:last-child,
  dl:last-child,
  table:last-child,
  blockquote:last-child {
    margin-bottom: 0; }

.o-wrapper {
  min-height: 100vh; }

.o-shell {
  max-width: 75rem;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto; }
  .o-shell--flex {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    flex-flow: row wrap; }

.o-grid {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-flow: row wrap; }
  .o-grid__item {
    flex: 0 0 100%; }
    .o-grid__item--1of2 {
      flex: 0 0 50%; }
    .o-grid__item--1of4 {
      flex: 0 0 25%; }
    .o-grid__item--1of3 {
      flex: 0 0 33.33333%; }
    .o-grid__item--2of3 {
      flex: 0 0 66.66667%; }
    .o-grid__item--1of5 {
      flex: 0 0 20%; }

.c-btn {
  text-align: center;
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  appearance: none; }
  .c-btn--block {
    width: 100%;
    display: block;
    padding-left: 0;
    padding-right: 0; }

[class^='ico-'] {
  @util text-hide;
  display: inline-block;
  vertical-align: middle; }

[class^='c-list-'] {
  padding: 0;
  margin: 0;
  list-style: none outside none; }

.alignleft {
  float: left;
  margin: 0.35rem 1.25rem 1.25rem 0; }

.alignright {
  float: right;
  margin: 0.35rem 0 1.25rem 1.25rem; }

.alignnone,
.aligncenter {
  float: none;
  margin: 0.35rem 0 1.25rem; }

.aligncenter {
  display: block; }

.text-left {
  text-align: left; }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.fullsize-background {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover; }



